"use client";

import React, { useState, useRef, useEffect } from "react";
import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";
import { MessageCircle, Send, AlertCircle, FileIcon, Upload } from "lucide-react";

interface Message {
  role: "user" | "assistant";
  content: string;
}

interface STLFileInfo {
  name: string;
  size: number;
  isValid?: boolean;
  dimensions?: {
    width: number;
    height: number;
    depth: number;
  };
  triangles?: number;
}

export default function STLChatbot({ 
  stlFileInfo, 
  fileName 
}: { 
  stlFileInfo?: STLFileInfo | null;
  fileName?: string;
}) {
  const [messages, setMessages] = useState<Message[]>([]);
  const [input, setInput] = useState("");
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const messagesEndRef = useRef<HTMLDivElement>(null);
  const fileInputRef = useRef<HTMLInputElement>(null);

  // ✅ USE stlFileInfo if available, fallback to fileName
  const activeFileInfo = stlFileInfo || (fileName ? { name: fileName, size: 0 } : null);

  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [messages]);

  const handleFileInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.currentTarget.files?.[0];
    if (file) {
      console.log('📁 Fichier sélectionné:', file.name);
      // Dispatch event for parent component to handle
      const event = new CustomEvent('stl-file-selected', { detail: { file } });
      document.dispatchEvent(event);
      // Reset input
      e.currentTarget.value = '';
    }
  };

  const triggerFileInput = () => {
    fileInputRef.current?.click();
  };

  const sendMessage = async () => {
    if (!input.trim() || loading) return;

    setError(null);
    const userMessage = input.trim();
    setInput("");

    const updatedMessages: Message[] = [
      ...messages,
      { role: "user", content: userMessage }
    ];
    setMessages(updatedMessages);
    setLoading(true);

    try {
      console.log('📤 Envoi chatbot:', {
        fileInfo: activeFileInfo,
        messageCount: updatedMessages.length,
        lastMessage: userMessage
      });

      const response = await fetch("/api/chat-stl", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          messages: updatedMessages,
          stlFileInfo: activeFileInfo || null,
        }),
      });

      const data = await response.json();

      console.log('📥 Réponse reçue:', { status: response.status, success: data.success });

      if (!response.ok) {
        throw new Error(data.error || "Erreur API");
      }

      setMessages((prev) => [
        ...prev,
        { role: "assistant", content: data.reply },
      ]);
    } catch (err) {
      const errorMsg = err instanceof Error ? err.message : "Erreur de connexion";
      setError(errorMsg);
      console.error("❌ Chat error:", err);
      setMessages((prev) => prev.slice(0, -1));
    } finally {
      setLoading(false);
    }
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === "Enter" && !e.shiftKey) {
      e.preventDefault();
      sendMessage();
    }
  };

  return (
    <Card className="w-full bg-white/10 backdrop-blur-md border border-white/20 flex flex-col h-full">
      <div className="flex flex-col h-full p-6">
        {/* Header */}
        <div className="flex items-center gap-3 mb-4 pb-4 border-b border-white/10 justify-between">
          <div className="flex items-center gap-3">
            <MessageCircle className="w-5 h-5 text-blue-300" />
            <div>
              <h3 className="font-semibold text-white text-sm md:text-base">
                Assistant STL - Perplexity
              </h3>
              {activeFileInfo ? (
                <div className="flex items-center gap-2 mt-1">
                  <FileIcon className="w-3 h-3 text-blue-300" />
                  <p className="text-xs text-blue-200">
                    📁 {activeFileInfo.name}
                    {activeFileInfo.size > 0 && ` (${(activeFileInfo.size / 1024).toFixed(2)} KB)`}
                  </p>
                </div>
              ) : (
                <p className="text-xs text-blue-300/70 mt-1">Aucun fichier uploadé</p>
              )}
            </div>
          </div>
          
          {!activeFileInfo && (
            <button
              onClick={triggerFileInput}
              className="px-3 py-1.5 bg-blue-600 hover:bg-blue-700 text-white rounded-lg text-xs font-semibold transition flex items-center gap-1 whitespace-nowrap"
              title="Uploader un fichier STL"
            >
              <Upload className="w-3.5 h-3.5" />
              Uploader
            </button>
          )}
        </div>

        {/* Hidden File Input */}
        <input
          ref={fileInputRef}
          type="file"
          accept=".stl"
          hidden
          onChange={handleFileInputChange}
        />

        {/* Messages Area */}
        <div className="flex-1 overflow-y-auto mb-4 space-y-3 min-h-0">
          {messages.length === 0 && !error && (
            <div className="text-center text-blue-100 text-sm py-8 flex flex-col items-center justify-center h-full">
              {activeFileInfo ? (
                <>
                  <p className="mb-4">👋 Fichier reçu: <strong>{activeFileInfo.name}</strong></p>
                  <p className="mb-6">Posez une question sur votre fichier 3D:</p>
                  <ul className="text-xs text-blue-200 space-y-2">
                    <li>• Quelle est la taille du fichier?</li>
                    <li>• Quelles sont les dimensions?</li>
                    <li>• Combien de triangles contient le modèle?</li>
                    <li>• Comment optimiser ce modèle?</li>
                    <li>• Est-ce prêt pour l'impression 3D?</li>
                  </ul>
                </>
              ) : (
                <>
                  <p className="mb-4 text-lg">👋 Bienvenue!</p>
                  <p className="mb-6">Uploadez un fichier STL pour commencer</p>
                  <button
                    onClick={triggerFileInput}
                    className="px-6 py-3 bg-gradient-to-r from-blue-600 to-blue-700 hover:from-blue-700 hover:to-blue-800 text-white rounded-lg font-semibold transition flex items-center gap-2 mx-auto shadow-lg"
                  >
                    <Upload className="w-5 h-5" />
                    Sélectionner un fichier STL
                  </button>
                  <p className="text-xs text-blue-300/60 mt-4">Ou glissez-déposez un fichier</p>
                </>
              )}
            </div>
          )}

          {messages.map((msg, idx) => (
            <div
              key={idx}
              className={`flex ${
                msg.role === "user" ? "justify-end" : "justify-start"
              }`}
            >
              <div
                className={`max-w-xs lg:max-w-md px-4 py-2 rounded-lg text-sm break-words ${
                  msg.role === "user"
                    ? "bg-blue-600 text-white rounded-br-none"
                    : "bg-white/10 text-blue-100 rounded-bl-none border border-white/10"
                }`}
              >
                <p>{msg.content}</p>
              </div>
            </div>
          ))}

          {loading && (
            <div className="flex justify-start">
              <div className="bg-white/10 text-blue-100 px-4 py-2 rounded-lg rounded-bl-none border border-white/10">
                <div className="flex gap-2">
                  <div className="w-2 h-2 bg-blue-300 rounded-full animate-bounce"></div>
                  <div
                    className="w-2 h-2 bg-blue-300 rounded-full animate-bounce"
                    style={{ animationDelay: "0.1s" }}
                  ></div>
                  <div
                    className="w-2 h-2 bg-blue-300 rounded-full animate-bounce"
                    style={{ animationDelay: "0.2s" }}
                  ></div>
                </div>
              </div>
            </div>
          )}

          {error && (
            <div className="flex justify-center">
              <div className="bg-red-500/20 border border-red-500/50 text-red-200 px-4 py-2 rounded-lg flex items-center gap-2 text-xs">
                <AlertCircle className="w-4 h-4 flex-shrink-0" />
                <span>{error}</span>
              </div>
            </div>
          )}

          <div ref={messagesEndRef} />
        </div>

        {/* Input Area */}
        <div className="flex gap-2 mt-auto">
          <input
            type="text"
            value={input}
            onChange={(e) => setInput(e.target.value)}
            onKeyPress={handleKeyPress}
            placeholder={activeFileInfo ? "Posez une question..." : "Uploadez un fichier d'abord"}
            disabled={loading || !activeFileInfo}
            className="flex-1 px-4 py-2 rounded-lg bg-white/10 border border-white/20 text-white placeholder-blue-200/50 focus:outline-none focus:border-blue-400 disabled:opacity-50 disabled:cursor-not-allowed text-sm"
          />
          <Button
            onClick={sendMessage}
            disabled={loading || !input.trim() || !activeFileInfo}
            className="bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded-lg disabled:opacity-50 disabled:cursor-not-allowed"
          >
            <Send className="w-4 h-4" />
          </Button>
        </div>
      </div>
    </Card>
  );
}